body {
    font-family: $font_primary;
    background-color: $color_body;
}
::selection {
    background: $color_primary;
    color: $color_white;
}
p,
ul,
li {
    font-family: $font_primary;
    color: $color_text_light;
}

p {
    font-size: 16px;
    line-height: 28px;
}

img {
    width: 100%;
    height: auto;
}

a {
    text-decoration: none;
    color: $color_primary;
    transition: color 0.2s;
    &:hover {
        text-decoration: none;
        color: $color_secondary;
    }
}

* {
    outline: 0px !important;
    -webkit-appearance: none;
}

*[id]:before {
    display: block;
    content: " ";
    margin-top: -120px; // Set the Appropriate Height
    height: 120px; // Set the Appropriate Height
    visibility: hidden;
}

h1,
h2,
h3,
h4,
h5 {
    font-family: $font_secondary;
    color: $color_primary;
}

h2 {
    font-weight: 300;
    strong {
        font-weight: bold;
    }
}

h2 {
    font-size: 32px;
}

section {
    padding: 80px 0;
}
.play-button {
    height: 80px;
    width: 80px;
    background-color: transparent;
    position: absolute;
    display: block;
    margin: 0 auto;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 2;
    border-radius: 50%;
    border: 2px solid $color_secondary_contrast;
    transition: background-color 0.2s, border 0.2s;
    i {
        color: $color_secondary_contrast;
        position: absolute;
        top: 50%;
        left: 52%;
        transform: translate(-50%, -50%);
        font-size: 28px;
        transition: color 0.2s;
    }
    &.audio-button {
        i {
            font-size: 41px;
        }
    }
}

.video-thumb {
    width: 100%;
    padding-bottom: 56.25%;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    cursor: pointer;
    filter: saturate(0);
    transition: filter 0.2s;
    .overlay {
        background-color: rgba($color: #000000, $alpha: 0.6);
        transition: background-color 0.2s;
    }
    &:hover {
        filter: saturate(1);
        .play-button {
            background-color: $color_secondary;
            border-color: $color_secondary;
            i {
                color: $color_secondary_contrast;
            }
        }
        .overlay {
            background-color: rgba($color: #000000, $alpha: 0.3);
        }
    }
}
.video-embed {
    display: none;
    padding-bottom: 56.25%;
    position: relative;
    iframe {
        left: 0;
        top: 0;
        height: 100%;
        width: 100%;
        position: absolute;
    }
}

.bt {
    border: 0px;
    border-radius: $border_radius;
    font-family: $font_primary;
    text-transform: uppercase;
    font-weight: bold;
    &:focus {
        outline: none;
    }
}

.bt-sombra {
    box-shadow: 0px 2px 15px 0px rgba(0, 0, 0, 0.3);
}

.bt-gradient {
    position: relative;
    z-index: 2;
    display: inline-block;
    &:before {
        border-radius: inherit;
        content: "";
        display: block;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0;
        width: 100%;
        z-index: -1;
        transition: opacity 0.5s;
    }
    &:hover {
        &:before {
            opacity: 1;
        }
    }
}

.bt-grande {
    padding: 12px 30px;
    font-size: 22px;
    line-height: 26px;
    @media screen and (max-width: 576px) {
        padding: 10px 30px;
        font-size: 18px;
        line-height: 22px;
    }
}

.bt-medio {
    padding: 10px 30px;
    font-size: 18px;
    line-height: 22px;
    @media screen and (max-width: 576px) {
        padding: 8px 25px;
        font-size: 16px;
        line-height: 20px;
    }
}

.bt-pequeno {
    padding: 8px 15px;
    font-size: 16px;
    line-height: 20px;
    @media screen and (max-width: 576px) {
        padding: 8px 12px;
        font-size: 16px;
        line-height: 20px;
    }
}

.bt-full {
    width: 100%;
    .bt {
        width: 100%;
    }
}

.bt-color_primary {
    background-color: $color_primary;
    color: $color_white;
    transition: background-color 0.2s;
    &:hover {
        background-color: $color_secondary;
    }
}

.bt-color_secondary {
    background-color: $color_secondary;
    color: $color_secondary_contrast;
    transition: background-color 0.2s;
    &:hover {
        background-color: $color_primary;
    }
}

.bt-color_primary_gradient {
    background: $color_primary_gradient;
    color: $color_white;
    &:before {
        background: $color_primary_gradient_invert;
    }
}

.overlay {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: 1;
}

.margin-center {
    margin: 0 auto;
    display: block;
}

.topo-interna {
    height: 200px;
    position: relative;
    .container {
        position: relative;
        height: 100%;
    }
    .conteudo {
        position: absolute;
        z-index: 2;
        top: 50%;
        transform: translateY(-50%);
        h1 {
            color: $color_white;
        }
    }
    .overlay {
        background-color: rgba(0, 0, 0, 0.3);
    }
    @media screen and (max-width: $md) {
        height: 160px;
    }
    @media screen and (max-width: $sm) {
        height: 120px;
    }
}

.pagination {
    display: block;
    margin: 0 auto;
    padding: 60px 0px;
    .page-numbers {
        background-color: $color_primary;
        text-align: center;
        display: inline-block;
        height: 35px;
        width: 35px;
        line-height: 18px;
        font-size: 18px;
        margin-right: 10px;
        color: $color_white;
        transition: opacity 0.2s;
        padding: 8px;
        vertical-align: middle;
        &:hover {
            opacity: 0.8;
        }
        &.current {
            background-color: $color_primary;
            &:hover {
                opacity: 1;
            }
        }
    }
    .next,
    .prev {
        padding: 12px;
        display: inline-block;
        font-family: "Flaticon";
        font-style: normal;
        font-weight: normal;
        font-variant: normal;
        line-height: 1;
        text-decoration: inherit;
        text-rendering: optimizeLegibility;
        text-transform: none;
        -moz-osx-font-smoothing: grayscale;
        -webkit-font-smoothing: antialiased;
        font-smoothing: antialiased;
        font-size: 11px;
        line-height: 11px;
    }
    .next {
        &:before {
            content: "\f108";
        }
    }
    .prev {
        &:before {
            content: "\f107";
        }
    }
}

.article-post {
    margin: 20px 0px;
    height: calc(100% - 40px);
    .thumbnail {
        position: relative;
        overflow: hidden;
        width: 100%;
        padding-top: 56.25%; /* 16:9 Aspect Ratio */
        img {
            transition: transform 0.3s;
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
        }
        .overlay {
        }
        &:hover {
            img {
                transform: scale(1.1);
            }
        }
    }
    .conteudo {
        &:hover {
            .titulo-post {
                color: $color_text;
            }
        }
    }
}

.wpcf7 {
    .wpcf7-not-valid-tip {
        font-size: 12px;
    }
    .wpcf7-response-output {
        border-radius: $border_radius;
        border: 0px;
        margin: 0px;
        margin-top: 20px;
        padding: 12px 25px;
        font-size: 14px;
        line-height: 18px;
        color: $color_white;

        &.wpcf7-mail-sent-ok {
            background-color: $color_success;
        }

        &.wpcf7-mail-sent-ng {
            background-color: $color_danger;
        }

        &.wpcf7-spam-blocked {
            background-color: $color_secondary;
        }

        &.wpcf7-validation-errors {
            background-color: $color_danger;
        }
    }
    .wpcf7-text,
    textarea {
        background-color: $color_bg_light;
        border: 1px solid $color_bg_dark;
        border-radius: $border_radius;
        padding: 5px 20px;
        width: 100%;
        color: $color_primary;
        font-size: 16px;
        font-family: $font_primary;
        &:focus {
            outline: 0px solid $color_secondary;
            border: 1px solid $color_secondary;
        }
    }
    .wpcf7-submit {
        width: 100%;
    }
    p {
        &:nth-last-of-type(1) {
            margin-bottom: 0px;
            position: relative;
        }
    }
    span.ajax-loader {
        background-image: none;
        position: absolute;
        top: -99px;
        right: 0px;
        z-index: 2;
        width: 40px;
        height: 40px;
        margin: 100px auto;
        background-color: $color_body;

        border-radius: 100%;
        -webkit-animation: sk-scaleout 1s infinite ease-in-out;
        animation: sk-scaleout 1s infinite ease-in-out;
    }

    @-webkit-keyframes sk-scaleout {
        0% {
            -webkit-transform: scale(0);
        }
        100% {
            -webkit-transform: scale(1);
            opacity: 0;
        }
    }

    @keyframes sk-scaleout {
        0% {
            -webkit-transform: scale(0);
            transform: scale(0);
        }
        100% {
            -webkit-transform: scale(1);
            transform: scale(1);
            opacity: 0;
        }
    }
}

.error404 {
    main {
        text-align: center;
        padding-top: 100px;
        padding-bottom: 100px;
        h1 {
            font-size: 200px;
            line-height: 150px;
            margin-bottom: 40px;
        }
    }
    @media screen and (max-width: 576px) {
        main {
            padding-top: 60px;
            padding-bottom: 60px;
            h1 {
                font-size: 120px;
                line-height: 100px;
            }
            h2 {
                font-size: 24px;
            }
        }
    }
}

@media screen and (max-width: $md) {
    body [data-aos][data-aos][data-aos-duration][data-aos-delay] {
        transition-delay: 0ms;
        transition-duration: 750ms;
    }
}

@media (min-width: $lg) {
    .wp-admin-space {
        margin-top: 32px !important;
    }
}

@media (max-width: $lg) {
    .wp-admin-space-menu-mobile {
        margin-top: 45px;
    }
}
