.blog, .single{
    .entry-header{
        height: 400px;
        position: relative;
        overflow: hidden;
        .container{
            position: relative;
            height: 100%;
            width: 100%;
        }
        .conteudo{
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            z-index: 2;
            h1, h2{
                color: $color_white;
                font-size: 42px;
                line-height: 58px;
                @media screen and (max-width: $lg){
                    font-size: 36px;
                    line-height: 46px;
                }
                @media screen and (max-width: $sm){
                    font-size: 32px;
                    line-height: 42px;
                }
            }
        }
        .overlay{
            background-color: rgba(0,0,0,0.4);
        }
    }
    .entry-content{
        padding: 60px 0px 30px;
        .resumo{
            p{
                font-size: 22px;
                line-height: 34px;
                font-weight: bold;
                color: $color_text_light;
            }
        }
        p{
            font-size: 18px;
            line-height: 34px;
            margin-bottom: 30px;
            @media screen and (max-width: $sm){
                font-size: 17px;
                line-height: 32px;
            }
        }
        h2{
            font-size: 38px;
            margin-top: 35px;
            margin-bottom: 20px;
            @media screen and (max-width: $lg){
                font-size: 32px;
                line-height: 44px;
            }
            @media screen and (max-width: $sm){
                font-size: 28px;
                line-height: 38px;
            }
        }
        h3{
            font-size: 30px;
            margin-top: 30px;
            margin-bottom: 15px;
            @media screen and (max-width: $lg){
                font-size: 26px;
                line-height: 32px;
            }
            @media screen and (max-width: $sm){
                font-size: 22px;
                line-height: 28px;
            }
        }
        h4{
            font-size: 24px;
            margin-top: 25px;
            margin-bottom: 10px;
            @media screen and (max-width: $lg){
                font-size: 20px;
                line-height: 25px;
            }
            @media screen and (max-width: $sm){
                font-size: 18px;
                line-height: 22px;
            }
        }
        a{
            word-break: break-all;
        }
    }
    .social-share{
        h3{
            display: inline-block;
            margin-right: 20px;
        }
        .social-icons{
            display: inline-block;
            i{
                font-size: 26px;
                margin: 0px 5px;
            }
        }
    }
    .mais-posts{
        padding: 30px 0px 60px;
        .titulo{
            font-size: 38px;
            margin-top: 35px;
            margin-bottom: 20px;
            @media screen and (max-width: $lg){
                font-size: 32px;
                line-height: 44px;
            }
            @media screen and (max-width: $sm){
                font-size: 28px;
                line-height: 34px;
            }
        }
    }
}

.page-template-blog{
    .lista-blog{
        padding: 40px 0px;
    }
}

.index{
    padding: 60px 0px;
}

.banner-principal{
    position: relative;
    overflow: hidden;
    height: 650px;
    .container, .row{
        height: 100%;
        position: relative;
        overflow: hidden;
        z-index: 2;
    }
    .slide{
        height: 650px;
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
        position: relative;

        overflow: hidden;
        &:focus{
            outline: none;
        }
        .coluna{
            position: relative;
        }
        .conteudo{
            h1, h2, h3, p{
                color: $color_white;
            }
            h1{
                font-size: 48px;
                line-height: 58px;
                margin-bottom: 40px;
            }
            p{
                font-size: 24px;
                line-height: 36px;
                margin-bottom: 30px;
            }
            .bt{
                margin-right: 15px;
                margin-top: 10px;
                margin-bottom: 10px;
            }
        }
        .overlay{
            background-color: rgba($color: #000, $alpha: 0.5);
        }
    }
    @media screen and (max-width: $lg){
        height: auto;
        .slide{
            height: auto;
        }
    }
    .slick-arrow{
        position: absolute;
        background-color: transparent;
        border: 0px solid transparent;
        height: 50px;
        width: 50px;
        top: 50%;
        transform: translateY(-50%);
        z-index: 4;
        opacity: 0;
        transition: opacity .2s;
        i{
            color: $color_white;
            font-size: 24px;
            transition: color .2s;
        }
        &.prev-slide{
            left: 0px;
        }
        &.next-slide{
            right: 0px;
        }
        &:focus{
            outline: none;
        }
        &:hover{
            i{
                color: $color_secondary;
            }
        }
    }
    .slick-dots{
        padding: 0px;
        margin: 0px;
        position: absolute;
        bottom: 30px;
        left: 50%;
        transform: translateX(-50%);
        z-index: 3;
        li{
            list-style: none;
            display: inline-block;
            margin: 0px 6px;
            button{
                height: 15px;
                width: 15px;
                font-size: 0px;
                background-color: $color_secondary_contrast;
                border-radius: 50%;
                border: 0px solid transparent;
                transition: background-color .2s, opacity .2s;
                &:focus{
                    outline: none;
                }
                &:hover{
                    opacity: 0.6;
                }
            }
            &.slick-active{
                button{
                    background-color: $color_secondary;
                    &:hover{
                        opacity: 0.6;
                    }
                }
            }
        }
        @media screen and (max-width: $sm){
            bottom: 30px;
            li{
                button{
                    height: 14px;
                    width: 14px;
                }
            }
        }
    }
    &:hover{
        .slick-arrow{
            opacity: 1;
        }
    }
}

