$color_primary: #cc3434;
$color_secondary: #171717;
$color_secondary_contrast: #ffffff;
$color_black: #000000;
$color_white: #ffffff;

$color_primary_gradient: linear-gradient(to right, #004f9a 0%, #007eb3 100%);
$color_primary_gradient_invert: linear-gradient(to left, #004f9a 0%, #007eb3 100%);

$color_secondary_gradient: linear-gradient(to right, #e67a10 0%, #f9b706 100%);
$color_secondary_gradient_invert: linear-gradient(to left, #e67a10 0%, #f9b706 100%);

$color_text: #000;
$color_text_light: #555;
$color_body: #ffffff;

$color_bg_light: #eaf0f7;
$color_bg_dark: #e4e9ee;

$color_success: #06be59;
$color_danger: #ff5656;
$color_warning: #fdd03d;

$xl: 1200px;
$lg: 992px;
$md: 768px;
$sm: 576px;

$font_primary: "Open Sans", sans-serif;
$font_secondary: "Merriweather", serif;

$border_radius: 5px;
