#footer{
    .top-footer{
        padding: 40px 0px;
        background-color: $color_secondary;
        .footer-coluna{
            margin: 15px 0px;
            .logo-alt{
                max-width: 200px;
                width: 100%;
            }
            .titulo-footer{
                color: $color_body;
            }
            a{
                color: $color_body;
                i{
                    margin-right: 8px;
                    font-size: 21px;
                    vertical-align: -2px;
                    transition: color .2s;
                }
                &:hover{
                    color: $color_primary;
                    i{
                        color: $color_primary;
                    }
                }
            }
            .info{
                color: $color_body;
            }
        }
        .redes-sociais{
            a{
                font-size: 24px;
                margin: 4px;
                &:hover{
                    color: $color_primary;
                }
                &:first-child{
                    margin-left: 0px;
                }
                &:last-child{
                    margin-right: 0px;
                }
            }
        }
        @media screen and (max-width: $md){
            text-align: center;
        }
    }
    .bottom-footer{
        background-color: $color_primary;
        padding: 30px 0px;
        text-align: center;
        .direitos{
            font-size: 14px;
            color: $color_body;
        }
    }
}