#header {
    box-shadow: 0 0px 8px rgba(0, 0, 0, 0.07);
    background-color: $color_body;
    width: 100%;
    padding: 15px 0;
    .navbar {
        padding-left: 0;
        padding-right: 0;
    }
    .navbar-brand {
        .logo {
            width: 128px;
            height: 53px;
            display: block;
        }
        .logo-alt {
            width: 128px;
            height: 53px;
            display: none;
        }
    }

    @media screen and (max-width: 992px) {
        .custom-logo {
            height: 40px;
            width: auto;
        }
    }
    &.transparent {
        background-color: transparent;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 5;
        box-shadow: none;
        .navbar-brand {
            .logo {
                display: none;
            }
            .logo-alt {
                display: block;
            }
        }
        .menu-header {
            li {
                a {
                    color: $color_white;
                    &:hover {
                        color: $color_primary;
                    }
                }
            }
        }
    }
    &.fixed-top {
        background-color: rgba($color_white, 0.95);
        position: fixed;
        .navbar-brand {
            .logo {
                width: 128px;
                height: 53px;
                display: block;
            }
            .logo-alt {
                width: 128px;
                height: 53px;
                display: none;
            }
        }
    }
}

@media (max-width: 992px) {
    .offcanvas-header {
        display: block !important;
    }
    .navbar-collapse {
        position: fixed;
        top: 0;
        bottom: 0;
        left: 100%;
        width: 100%;
        padding-right: 1rem;
        padding-left: 1rem;
        overflow-y: auto;
        visibility: hidden;
        background-color: $color_black;
        transition: visibility 0.2s ease-in-out, transform 0.2s ease-in-out;
        z-index: 99;
        word-break: break-all;
    }
    .navbar-collapse.show {
        visibility: visible;
        transform: translateX(-100%);
    }
}
.navbar-toggler {
    padding: 0;
    margin: 0;
    border: none;
    border-radius: 0;
}

.navbar-toggler-icon {
    background-image: url(./imgs/menu-bars.svg);
}

.btn-close {
    color: $color_white;
    font-size: 40px!important;
    margin: 0!important;
    padding: 0!important;
    width: 40px!important;
    line-height: 1!important;
    height: 40px!important;
    border-radius: 0!important;
}

body.offcanvas-active {
    overflow: hidden;
}
.offcanvas-header {
    display: none;
}

.whatsapp-flutuante {
    background-image: url(imgs/whatsapp.png);
    background-size: cover;
    background-position: center center;
    position: fixed;
    bottom: 40px;
    right: 40px;
    z-index: 5;
    height: 50px;
    width: 50px;
    text-align: center;
    transition: opacity 0.5s;
    filter: drop-shadow(0px 0px 5px rgba(0, 0, 0, 0.4));
    a {
        display: inline-block;
        width: 100%;
        height: 100%;
    }
    i {
        color: $color_white;
        font-size: 21px;
        line-height: 21px;
    }
    &:hover {
        opacity: 0.6;
    }
    @media screen and (max-width: $sm) {
        bottom: 20px;
        right: 20px;
    }
}
